    body: `Artık çiftler düğünlerini ister işletmenizdeki aktüel kamera
    görüntülerini, isterlerse cep telefonlarının kamerasını kullanarak tüm
    dünyaya HD kalitesinde canlı yayınlayabilecekler. Bu sayede uzaktakiler
    veya bir sebeple düğüne katılamayanlar e-düğün üzerinden TV, PC, tablet
    ya da telefonlarından oldukları yerden çiftin mutluluğuna ortak
    olabilecekler.`,
