    body: `Online alışveriş kültürüne sahip düğün planlayan kullanıcılar, teminatlarını e-düğün sistemine bloke ederek, belirledikleri bütçe ve düğünlerine ait bilgilerle yine kendilerinin belirlemiş olduğu mekanlar arasında ihale yapabilecekler. Eğer kullanıcının teklifini kabul eden bir mekan olursa, 5 iş günü içerisinde bütün ödemeyi yapma ya da teminatının İşletme tarafındanirat kaydedilmesi arasında bir tercih yapmak durumunda.`,
