    body: `Bu özelliği kullanmak isterseniz, Müşterileriniz için çok zengin resimli menüler sunabilirsiniz. Düğün Planlayanlar, oturdukları yerden kendi düğün ve kokteyl menülerini istedikleri gibi hazırlayabilecekler. Sizin yapmanız gereken sadece ürün bazında birim fiyatlarınızı ve porsiyon bilgilerinizi girmek. Dilerseniz sizde kendinize ait ikram ve yemek çeşitleri ve bunlara ait resim yükleyebilir ya da sunduğumuz menüden istemediğiniz ürünleri kaldırabilirsiniz. `,
