    body: `Düğün merasimlerinin olmazsa olmazı takı takma, e-düğünle artık çok daha kolay. Düğün planlayan çiftler, e-düğün sayfalarının e-takı bölümünden davetlilerine online olarak ister altın ya da para gönderebilme isterlerse de çiftin beğendiği ve yeni evlerinde kullanacakları bazı gereksinimlerini kısmen ya da tamamen hediye etme imkanını sağlayacaklar. Türkiye’nin çok önemli markalarının yer aldığı bu platform çok beğenilecek.`,
