    body: `Hayatlarında ilk defa böylesine büyük bir organizasyonu yapacak olan çiftlere e-düğün ajandası tüm süreçlerinde yardımcı olacak. Bir düğüne dair, kız isteme sürecinden, söz, nişan süreçlerine, mekan tercihlerinden, gelinlik tercihlerine, düğün gününden, balayı planlamalarına kadar e-ajanda onların hep yanında olacak ve tüm bu süreçleri kolayca ve maliyetlerini kontrol altında tutarak tamamlamalarına yardımcı olacak. `,
