    body: `Düğün Planlayanların yapacakları sadece, davetli bilgilerini telefonlarından ya da manuel olarak LCV e-takip sistemine yüklemek. Geri kalan tüm takip e-düğün tarafından yapılacak. Sistem tüm davetlilere, e-mail ve sms üzerinden ulaşarak, davete katılım durumlarını takip edecek ve kullanıcıya anlık katılım durumlarını ücretsiz bir şekilde raporlayacak. Ayrıca düğün günü tüm konukların HES kodlarını entegre olduğu sistemden sorgulayacak ve hesap sahibine raporlayacak.`,
    isFree: true,
